.modal-mobile {
  position: fixed;
  height: 100dvh;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  display: flex;
  align-items: flex-end;
  background: rgba(23, 23, 23, 0.9);
  opacity: 0;
  visibility: hidden;
  transition: all ease-in-out 0.4s;

  &.active {
    opacity: 1;
    visibility: visible;
    transition: all ease-in-out 0.4s;
  }

  &-inner {
    width: 100%;
  }

  &-list {
    display: flex;
    flex-direction: column;
    width: 100%;
    overflow: hidden;
    border-radius: 12px 12px 0px 0px;
    border-top: 1px solid rgba(231, 178, 93, 0.09);
    border-bottom: 1px solid rgba(231, 178, 93, 0.09);
    border-left: 1px solid rgba(231, 178, 93, 0.09);
    backdrop-filter: blur(17px);
  }

  &-btn {
    text-transform: uppercase;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: var(--g8);
    width: 100%;
    background: rgba(0, 0, 0, 0.6);

    height: 18.88vw;
  }

  &-btn--open {
    position: relative;
    &::before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background: linear-gradient(
        270deg,
        rgba(245, 223, 186, 0) 0%,
        #f5dfba 50%,
        rgba(143, 130, 109, 0) 100%
      );
      z-index: 1;
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background: linear-gradient(
        270deg,
        rgba(245, 223, 186, 0) 0%,
        #f5dfba 50%,
        rgba(143, 130, 109, 0) 100%
      );
      z-index: 1;
    }
  }

  &-btn--close {
    span {
      color: var(--color-pantone-1);
    }
  }
}
