@use '../assets/mixins.scss' as *;

.single-product-hero {
  height: 100%;
  padding: var(--p30) var(--p291) 0 var(--p291);
  &-inner {
    display: flex;
    gap: var(--g60);
    padding: var(--p30);
    background: var(--white);
    border-radius: 20px;
  }

  &-swiper-wrap {
    width: vw(518, 1920);
    height: vw(447, 1920);
  }

  &-swiper {
    width: 100%;
    height: 100%;
    .swiper-slide {
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
  }

  &-swiper-thumbs-wrap {
    display: flex;
    align-items: center;
    gap: var(--g24);
  }

  &-swiper-thumbs-btn-prev {
    border: 1px solid var(--basic-grey);
    border-radius: 20px;
    height: vw(70, 1920);
    width: vw(20, 1920);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    &.swiper-button-disabled {
      opacity: 0.5;
      pointer-events: none;
    }

    svg {
      height: vw(18, 1920);
      width: vw(18, 1920);
      fill: var(--mid-grey);
      transform: rotate(180deg);
      transition: all 0.3s ease-in-out;
    }
  }

  &-swiper-thumbs-btn-next {
    border: 1px solid var(--basic-grey);
    border-radius: 20px;
    height: vw(70, 1920);
    width: vw(20, 1920);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    &.swiper-button-disabled {
      opacity: 0.5;
      pointer-events: none;
    }

    svg {
      height: vw(18, 1920);
      width: vw(18, 1920);
      fill: var(--mid-grey);

      transition: all 0.3s ease-in-out;
    }
  }

  &-swiper-thumbs {
    width: 100%;
  }

  &-swiper-thumbs-slide {
    width: vw(70, 1920);
    height: vw(70, 1920);
    border-radius: 12px;
    overflow: hidden;
    cursor: pointer;
    border: 1px solid var(--basic-grey);
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--white);

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      transition: all 0.3s ease-in-out;
    }

    &.swiper-slide-thumb-active {
      border: 1px solid #00c1c4;

      img {
        transform: scale(1.1);
      }
    }
  }

  &-info-wrap {
    display: flex;
    flex-direction: column;
    gap: var(--g30);
  }

  &-title-wrap {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: var(--g24);
  }

  &-rate-wrap {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }

  &-rate {
    display: flex;
    align-items: center;
    gap: var(--g16);
  }

  &-rate-stars {
    display: flex;

    &--1 {
      svg {
        &:first-child {
          fill: var(--black);
        }
      }
    }
    &--2 {
      svg {
        &:nth-child(-n + 2) {
          fill: var(--black);
        }
      }
    }

    &--3 {
      svg {
        &:nth-child(-n + 3) {
          fill: var(--black);
        }
      }
    }

    &--4 {
      svg {
        &:nth-child(-n + 4) {
          fill: var(--black);
        }
      }
    }

    &--5 {
      svg {
        &:nth-child(-n + 5) {
          fill: var(--black);
        }
      }
    }

    svg {
      width: vw(18, 1920);
      height: vw(18, 1920);
      fill: var(--basic-grey);
    }
  }

  &-sku {
    display: flex;
    padding: vw(6, 1920) vw(16, 1920);
    justify-content: center;
    align-items: center;
    gap: var(--g10);
    border-radius: 12px;
    background: var(--light-grey);
    cursor: pointer;
    pointer-events: all;
    transition: all 0.3s ease-in-out;

    &:hover {
      background: var(--yellow);
      box-shadow: 0px 0px 25px 0px rgba(246, 255, 0, 0.25);
    }
    svg {
      width: vw(16, 1920);
      height: vw(16, 1920);
      pointer-events: none;
    }
  }

  &-descr-wrap {
    &.expanded {
      .single-product-hero-descr {
        display: block;
        -webkit-line-clamp: unset;
      }
    }
    .single-product-hero-descr {
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  &-descr-more {
    cursor: pointer;
  }

  &-btns-wrap {
    display: flex;
    align-items: center;
    gap: var(--g12);
  }

  &-favourite {
    border-radius: 50%;
    width: calc(48 / 1920 * 100vw);
    height: calc(48 / 1920 * 100vw);
    background: var(--light-grey);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease-in-out;
    cursor: pointer;

    &.active {
      background: #ffe8e8;

      svg {
        path {
          stroke: #ff3b3b;
          fill: #ff3b3b;
        }
      }
    }

    &:hover {
      background: #ffe8e8;

      svg {
        transform: scale(1.1);
        path {
          stroke: #ff3b3b;
          fill: #ff3b3b;
        }
      }
    }

    svg {
      width: calc(18 / 1920 * 100vw);
      height: calc(18 / 1920 * 100vw);
      transition: all 0.3s ease-in-out;
      path {
        stroke: var(--black);
        transition: all 0.3s ease-in-out;
      }
    }
  }

  &-customer-info-list {
    display: flex;
    gap: var(--g12);
  }

  &-customer-info-item {
    display: flex;
    align-items: center;
    gap: var(--g12);
    svg {
      width: vw(16, 1920);
      height: vw(16, 1920);
      fill: var(--basic-grey);
    }
  }

  &-info-list {
    display: flex;
    flex-direction: column;
    gap: var(--g12);
  }

  &-info-item {
    width: fit-content;
    display: flex;
    align-items: center;
    gap: var(--g12);
    padding: vw(12, 1920) vw(24, 1920);
    border-radius: 1234px;
    background: var(--light-grey);
    svg {
      width: vw(18, 1920);
      height: vw(18, 1920);
      fill: var(--basic-grey);
    }
  }
}
