@use '../assets/mixins.scss' as *;
@use '../assets/vars.scss' as *;
.cart {
  padding: var(--p30) var(--p291) 0 var(--p291);
  display: flex;
  justify-content: space-between;
  &-title {
    padding: var(--p30) var(--p291) 0 var(--p291);
  }
  &-list-wrap {
    display: flex;
    flex-direction: column;
    padding: var(--p30);
    background: var(--white);
    border-radius: 20px;
    width: vw(868, 1920);
  }

  &-list-title-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &-list-title {
    display: flex;
    align-items: center;
    gap: var(--g12);
  }

  &-list-btn-back {
    display: flex;
    align-items: center;
    gap: var(--g12);

    svg {
      width: vw(18, 1920);
      height: vw(18, 1920);
    }
  }

  &-line {
    width: 100%;
    height: 1px;
    background-color: var(--light-grey);
    margin-top: var(--g24);
  }

  &-list {
    display: flex;
    flex-direction: column;
  }

  &-item {
    padding: var(--g24) 0;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &:not(:first-child) {
      border-top: 1px dashed var(--light-grey);
    }
  }

  &-item-title-wrap {
    display: flex;
    align-items: center;
    gap: var(--g16);
  }

  &-item__img {
    width: vw(80, 1920);
    height: vw(80, 1920);
    border-radius: 20px;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &-item-title {
    width: vw(200, 1920);
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &-item__count {
    display: flex;
    align-items: center;
    gap: var(--g12);
  }

  &-item__count-btn {
    width: vw(22, 1920);
    height: vw(22, 1920);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: var(--light-grey);

    cursor: pointer;

    svg {
      width: vw(14, 1920);
      height: vw(14, 1920);
      fill: var(--blue-2);
    }
  }

  &-item__count-input {
    appearance: none;
    -webkit-appearance: none;
    border: none;
    background: transparent;
    width: vw(40, 1920);
    text-align: center;
    color: var(--black);
    font-family: $default-font2;
    font-size: vw(14, 1920);
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  &-item__price {
    width: vw(160, 1920);
    display: flex;
    justify-content: center;
  }

  &-item__actions {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &-item__actions__btn {
    width: vw(18, 1920);
    height: vw(18, 1920);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    svg {
      width: vw(14, 1920);
      height: vw(14, 1920);
      fill: var(--blue-2);
      pointer-events: none;
    }
  }

  &-summary {
    height: fit-content;
    width: vw(440, 1920);
    display: flex;
    flex-direction: column;
    gap: var(--g24);
    padding: var(--p30);
    background: var(--white);
    border-radius: 20px;
  }

  &-summary__title-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &-summary__btn {
    width: 100%;
  }

  &-summary-line {
    width: 100%;
    height: 1px;
    background-color: var(--light-grey);
  }
  &-summary__info {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: var(--g12);

    svg {
      width: vw(20, 1920);
      height: vw(20, 1920);
    }
    span {
      width: 95%;
    }
  }
}
