@use '../../assets/mixins' as *;

.swiper-button {
  &-nav {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: var(--g8);
  }

  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  height: vw(44, 1920);
  border-radius: 1234px;
  padding: vw(10, 1920) vw(16, 1920);
  border: 1px solid var(--black);
  transition: background-color 0.3s ease;

  &:hover {
    background-color: var(--black);

    svg {
      fill: var(--white);
    }
  }

  svg {
    width: vw(18, 1920);
    height: vw(18, 1920);
    fill: var(--black);
  }

  &--prev {
    svg {
      transform: rotate(180deg);
    }
  }

  &.swiper-button-disabled {
    pointer-events: none;
    border: 1px solid var(--basic-grey);

    svg {
      fill: var(--basic-grey);
    }
  }
}
