@use '../assets/mixins.scss' as *;
.category-homepage {
  position: relative;
  width: 100%;
  height: 100%;
  background: var(--bg-grey);

  &-inner {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: var(--g60);
    padding: var(--p160) var(--p291);
  }

  &-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-column-gap: var(--g30);
    grid-row-gap: var(--g30);
  }

  &__item {
    display: flex;
    text-decoration: none;
    position: relative;
    width: 100%;
    height: vw(248, 1920);
    display: flex;
    padding: var(--p40);
    background: var(--white);
    border-radius: 20px;
    overflow: hidden;

    &:hover {
      .category-homepage__item-icon-svg {
        transform: translateX(50%);
      }
    }

    &--1 {
      grid-area: 1 / 1 / 2 / 2;
    }
    &--2 {
      grid-area: 1 / 2 / 2 / 4;
      background: var(--bg-black);

      .category-homepage__item-title {
        width: 40%;
      }
    }
    &--3 {
      grid-area: 2 / 1 / 3 / 2;
    }
    &--4 {
      grid-area: 2 / 2 / 3 / 3;
    }
    &--5 {
      grid-area: 2 / 3 / 4 / 4;
      height: vw(526, 1920);
      .category-homepage__item-title {
        width: 40%;
      }
    }
    &--6 {
      grid-area: 3 / 1 / 4 / 2;
      background: var(--bg-black);
    }
    &--7 {
      grid-area: 3 / 2 / 4 / 3;
      background: var(--bg-black);
    }
  }

  &__item-inner {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__item-img-wrap {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
  }

  &__item-img--granade--1 {
    position: absolute;
    top: -2%;
    right: 7%;
    width: calc(280 / 1920 * 100vw);
    height: auto;
    img {
      position: relative;
      width: 100%;
      height: auto;
      transform: rotate(-2.316deg);
    }
  }
  &__item-img--granade--2 {
    position: absolute;
    top: -30%;
    right: 0%;
    width: vw(144, 1920);
    height: auto;
    img {
      position: relative;
      width: 100%;
      height: auto;
    }
  }
  &__item-img--granade--3 {
    position: absolute;
    top: -16%;
    right: 33%;
    width: vw(180, 1920);
    height: auto;

    img {
      position: relative;
      width: 100%;
      height: auto;
      transform: rotate(1.316deg);
    }
  }

  &__item-img--granade--4 {
    position: absolute;
    top: 15%;
    right: -20%;
    width: vw(392, 1920);
    height: auto;

    img {
      position: relative;
      width: 100%;
      height: auto;
      transform: rotate(-15.684deg);
    }
  }
  &__item-img--granade--5 {
    position: absolute;
    top: 37%;
    right: 44%;
    width: vw(200, 1920);
    height: auto;

    img {
      position: relative;
      width: 100%;
      height: auto;
      transform: rotate(15.684deg);
    }
  }

  &__item-img--mine {
    position: absolute;
    top: -27%;
    right: -20%;
    width: calc(320 / 1920 * 100vw);
    height: auto;
    transform: rotate(-11.684deg);
    img {
      position: relative;
      width: 100%;
      height: auto;
      transform: rotate(-11.684deg);
    }
  }

  &__item-link {
    display: flex;
    align-items: center;
    gap: var(--g20);
    text-decoration: none;
  }

  &__item-icon {
    width: vw(40, 1920);
    height: vw(6, 1920);
    display: flex;
    svg {
      width: 100%;
      height: auto;
    }
  }

  &__item-icon-svg {
    transform: translateX(0);
    transition: transform 0.3s ease-in-out;
    width: 100%;
    height: auto;
  }
}

.companies-homepage {
  position: relative;
  width: 100%;
  height: vw(63, 1920);
  background: var(--bg-grey);
  overflow: hidden;
  display: flex;
  align-items: center;

  &-list {
    position: absolute;
    height: 100%;
    width: 200%;
    display: flex;
    align-items: center;
    gap: vw(100, 1920);
  }

  &-item {
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      height: vw(63, 1920);
      width: auto;
    }
  }
}
