@use '../assets/mixins.scss' as *;

.about-numbers {
  position: relative;
  width: 100%;
  height: vw(550, 1920);
  z-index: 10;
  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #1f1e1a;
    z-index: 0;
  }

  &__bg--left {
    position: absolute;
    bottom: -12%;
    left: 0;
    width: vw(600, 1920);
    height: auto;
  }

  &__bg--right {
    position: absolute;
    top: -15%;
    right: 0;
    width: vw(600, 1920);
    height: auto;
  }

  &__inner {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: var(--g80);
    z-index: 1;
    padding: vw(60, 1920) var(--p291);
  }

  &-list {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }

  &-item {
    padding: var(--p60);
    width: 33.33%;
    height: vw(215, 1920);
    display: flex;
    flex-direction: column;
    gap: var(--g24);
    &:first-child {
      width: 66.66%;
      flex-direction: row;
      gap: var(--g16);
      align-items: flex-end;
      border-bottom: 1px dashed #42474a;
      border-right: 1px dashed #42474a;
      .about-numbers-item__number {
        font-size: vw(72, 1920);
      }
    }

    &:nth-child(2) {
      border-bottom: 1px dashed #42474a;
    }

    &:nth-child(4) {
      border-left: 1px dashed #42474a;
      border-right: 1px dashed #42474a;
    }
  }

  &-item__number {
    line-height: 80%;
  }

  &-item__text {
    text-transform: none;
    font-weight: 400;
  }
}
