@use '../assets/mixins.scss' as *;

.review-card {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--g16);
  padding: var(--p30);
  border-radius: 30px;
  background: var(--white);

  &__header {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 1px;
  }

  &__name {
    text-transform: none;
  }

  &__time {
    text-transform: none;
  }

  &__rate-wrap {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: var(--g8);
  }

  &__rate {
    width: 100%;
    display: flex;
    align-items: center;
    gap: calc(16 / 1920 * 100vw);
  }

  &__rate {
    display: flex;
    align-items: center;
    gap: var(--g5);

    svg {
      width: vw(18, 1920);
      height: vw(18, 1920);
      fill: var(--basic-grey);
    }

    &--1 {
      svg {
        &:first-child {
          fill: #ffcc00;
        }
      }
    }
    &--2 {
      svg {
        &:nth-child(-n + 2) {
          fill: #ffcc00;
        }
      }
    }

    &--3 {
      svg {
        &:nth-child(-n + 3) {
          fill: #ffcc00;
        }
      }
    }

    &--4 {
      svg {
        &:nth-child(-n + 4) {
          fill: #ffcc00;
        }
      }
    }

    &--5 {
      svg {
        &:nth-child(-n + 5) {
          fill: #ffcc00;
        }
      }
    }
  }

  &__text {
    text-transform: none;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
