@use '../assets/mixins.scss' as *;
.category {
  &-all-sections {
    display: flex;
    flex-direction: column;
  }
  &-wrap {
    padding: var(--p30) var(--p291) 0 var(--p291);
    display: flex;
    justify-content: space-between;
  }

  &-main {
    width: calc(75% - (var(--g30) / 2));
    display: flex;
    flex-direction: column;
    gap: var(--g30);
  }

  &-list-inner {
    display: flex;
    flex-wrap: wrap;
    gap: var(--g30);
  }

  &-list__item {
    width: calc(33.33% - (var(--g30) * 2 / 3));
  }

  &-filter-wrap {
    height: fit-content;
    display: flex;
    flex-direction: column;
    width: calc(25% - (var(--g30) / 2));
    padding: var(--p30);
    gap: var(--g24);
    background: var(--white);
    border-radius: 20px;
  }

  &-filter {
    display: flex;
    flex-direction: column;
    gap: calc(var(--g24) * 2);
  }

  &-filter-section {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 0;
    transition: all 0.3s ease-in-out;

    &:not(:first-child) {
      &::before {
        content: '';
        position: absolute;
        top: calc(-1 * var(--g24));
        left: 0;
        transform: translateY(50%);
        width: 100%;
        height: 1px;
        background-color: var(--light-grey);
      }
    }

    [data-group] {
      height: 0;
      max-height: 0;
      opacity: 0;
      overflow: hidden;
      pointer-events: none;
      /* Add transition for smooth animation */
      transition: all 0.3s ease-in-out;
    }

    &.active {
      gap: var(--g16);

      [data-group] {
        height: auto;
        max-height: 1000px; /* Set a large max-height instead of fit-content for animation */
        opacity: 1;
        overflow: visible;
        pointer-events: auto;
      }
    }
  }

  &-filter-section__title {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &.active {
      svg {
        transform: rotate(180deg);
      }
    }

    svg {
      width: vw(18, 1920);
      height: vw(18, 1920);
      transition: all 0.3s ease-in-out;
    }
  }

  &-filter-price {
    display: flex;
    align-items: center;
    gap: var(--g10);
  }

  &-filter-price__input {
    padding: vw(10, 1920) vw(16, 1920);
    border-radius: 1243px;
    border: 1px solid #cbd5e1;
    width: vw(112.5, 1920);
    height: vw(40, 1920);
    font-size: vw(14, 1920);
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    text-align: center;
    color: var(--black);
  }

  &-filter-group {
    display: flex;
    flex-direction: column;
    gap: var(--g12);
  }

  &-filter-group__label {
    display: flex;
    align-items: center;
    position: relative;
    gap: var(--g12);

    input:checked + .category-filter-group__svg {
      opacity: 1;
    }

    input:checked + .text-deep-grey {
      color: var(--blue-2);
    }
  }

  &-filter-group__input {
    appearance: none;
    -webkit-appearance: none;
    background: var(--white);
    cursor: pointer;
    position: relative;
    width: vw(20, 1920);
    height: vw(20, 1920);
    border-radius: vw(8, 1920);
    border: 1px solid #cbd5e1;
    pointer-events: all;

    &:checked {
      background: var(--blue-2);
      border: 1px solid var(--blue-2);
    }
  }

  &-filter-group__svg {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(35%, -50%);
    width: vw(12, 1920);
    height: vw(12, 1920);
    fill: none;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.2s ease-in-out;
  }

  &-views {
    padding: var(--p160) var(--p291) 0 var(--p291);
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--g60);
  }

  &-views__title-wrap {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    gap: var(--g20);
  }

  &-views__btns-wrap {
    display: flex;
    align-items: center;
    gap: vw(16, 1920);
  }

  &-views-list-wrap {
    width: 100%;
  }

  &-views-list {
    width: 100%;
  }

  &-additional-text-wrap {
    display: flex;
    flex-direction: column;
    gap: var(--g30);
    padding: var(--p160) var(--p291);
  }

  &-additional-text {
    display: inline-block;

    &.expanded p {
      -webkit-line-clamp: unset; /* Remove line clamp */
      max-height: none; /* Allow full height */
    }
    p {
      display: -webkit-box;
      -webkit-line-clamp: 3; /* Limit to 3 lines */
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  &-read-more {
    cursor: pointer;
  }
}
