@use '../assets/mixins.scss' as *;

.single-product-details {
  width: 100%;
  //   height: 100vh;
  padding: var(--p30) var(--p291) 0 var(--p291);
  display: flex;
  align-items: flex-start;
  gap: var(--g30);
  position: relative;
  //   overflow-y: hidden;

  &-card {
    align-self: flex-start;
    // position: sticky;
    // top: 0;
    display: flex;
    flex-direction: column;
    gap: var(--g20);
    padding: var(--p30);
    background: var(--white);
    border-radius: 20px;
    width: vw(440, 1920);
    height: vw(380, 1920);
  }

  &-card-inner {
    display: flex;
    flex-direction: column;
    gap: var(--g24);
  }

  &-card-title-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &-card-title-favourite {
    border-radius: 50%;
    width: calc(44 / 1920 * 100vw);
    height: calc(44 / 1920 * 100vw);
    background: var(--light-grey);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease-in-out;
    cursor: pointer;

    &.active {
      background: #ffe8e8;

      svg {
        path {
          stroke: #ff3b3b;
          fill: #ff3b3b;
        }
      }
    }

    &:hover {
      background: #ffe8e8;

      svg {
        transform: scale(1.1);
        path {
          stroke: #ff3b3b;
          fill: #ff3b3b;
        }
      }
    }

    svg {
      width: calc(18 / 1920 * 100vw);
      height: calc(18 / 1920 * 100vw);
      transition: all 0.3s ease-in-out;
      path {
        stroke: var(--black);
        transition: all 0.3s ease-in-out;
      }
    }
  }

  &-card-line {
    width: 100%;
    height: 1px;
    background: var(--light-grey);
  }

  &-card-img-wrap {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: var(--g24);
    position: relative;
  }

  &-card-img {
    width: vw(80, 1920);
    height: vw(80, 1920);
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #d8dfe0;
    border-radius: 12px;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &-card-price-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &-card-btns-wrap {
    button {
      width: 100%;
    }
  }

  &-list {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: var(--g30);
    width: vw(868, 1920);
    height: 100%;

    // min-height: 100%;
  }

  &-item {
    width: 100%;
    display: flex;
    flex-direction: column;

    padding: var(--p30);
    background: var(--white);
    border-radius: 20px;
    transition: all 0.3s ease-in-out;

    &.active {
      gap: var(--g24);
      .single-product-details-item-title-wrap {
        .single-product-details-item-title-icon {
          svg {
            transform: rotate(-90deg);
          }
        }
      }

      .single-product-details-item-content {
        max-height: 5000px;
        overflow: visible;
      }
      .single-product-details-item-line {
        display: block;
      }
    }
  }

  &-item-content {
    max-height: 0;
    overflow: hidden;
  }

  &-item-title-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: var(--p10) 0;
  }

  &-item-title-icon {
    width: calc(18 / 1920 * 100vw);
    height: calc(18 / 1920 * 100vw);
    display: flex;
    align-items: center;
    justify-content: center;

    transition: all 0.3s ease-in-out;
    cursor: pointer;

    svg {
      transform: rotate(90deg);
      width: 100%;
      height: 100%;
      fill: var(--deep-grey);
      transition: all 0.3s ease-in-out;
    }
  }

  &-item-line {
    display: none;
    width: 100%;
    height: 1px;
    background: var(--light-grey);
  }

  &-item-list {
    display: flex;
    flex-direction: column;
    border-radius: 18px;
    overflow: hidden;
  }

  &-item-list-item {
    display: flex;
    padding: vw(19, 1920) var(--p30);
    background-color: var(--white);

    &:nth-child(odd) {
      background-color: var(--light-grey);
    }
    span {
      width: 50%;
    }
  }

  &-item-review-list {
    display: flex;
    flex-direction: column;
    gap: var(--g30);
  }

  &-item-review {
    display: flex;
    flex-direction: column;
    gap: var(--g24);
    background: var(--light-grey);
    border-radius: 20px;
    padding: var(--p30);
  }

  &-item-review-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &-item-review-header-rate-wrap {
    display: flex;

    align-items: center;
    gap: var(--g8);
  }

  &-item-review-header-rate-stars {
    display: flex;
    gap: var(--g4);

    svg {
      width: calc(18 / 1920 * 100vw);
      height: calc(18 / 1920 * 100vw);
      fill: var(--basic-grey);
      transition: all 0.3s ease-in-out;
    }

    &--1 {
      svg {
        &:first-child {
          fill: var(--black);
        }
      }
    }

    &--2 {
      svg {
        &:nth-child(-n + 2) {
          fill: var(--black);
        }
      }
    }

    &--3 {
      svg {
        &:nth-child(-n + 3) {
          fill: var(--black);
        }
      }
    }

    &--4 {
      svg {
        &:nth-child(-n + 4) {
          fill: var(--black);
        }
      }
    }

    &--5 {
      svg {
        &:nth-child(-n + 5) {
          fill: var(--black);
        }
      }
    }
  }

  &-item-review-images {
    display: flex;
    align-items: center;
    gap: var(--g16);
    img {
      width: calc(80 / 1920 * 100vw);
      height: calc(80 / 1920 * 100vw);
      object-fit: cover;
      border-radius: 12px;
    }
  }

  &-item-review-comment {
    display: flex;
    flex-direction: column;
    gap: var(--g16);
  }

  &-item-review-comment-inner {
    display: flex;
    align-items: center;

    gap: var(--g8);
    svg {
      width: calc(16 / 1920 * 100vw);
      height: calc(16 / 1920 * 100vw);
      transition: all 0.3s ease-in-out;
    }
  }

  &-item-faq-list {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: var(--g12);
  }

  &-item-faq {
    display: flex;
    flex-direction: column;
    // gap: var(--g12);
    background: var(--light-grey);
    border-radius: 18px;
    padding: vw(12, 1920) vw(24, 1920);
    transition: all 0.3s ease-in-out;
    cursor: pointer;

    &.active {
      gap: var(--g12);

      .single-product-details-item-faq-header {
        svg {
          transform: rotate(-90deg);
        }
      }
      .single-product-details-item-faq-text {
        max-height: 400px;
        overflow: visible;
      }
    }
  }

  &-item-faq-header {
    display: flex;
    justify-content: space-between;
    align-items: center;

    svg {
      transform: rotate(90deg);
      width: calc(18 / 1920 * 100vw);
      height: calc(18 / 1920 * 100vw);
      fill: var(--deep-grey);
      transition: all 0.3s ease-in-out;
      cursor: pointer;
    }
  }

  &-item-faq-header-title {
    display: flex;
    align-items: center;
    gap: var(--g8);

    .text-14-k {
      line-height: 90%;
    }
  }

  &-item-faq-text {
    max-height: 0;
    overflow: hidden;
    transition: all 0.3s ease-in-out;
  }
}
