@use './assets/mixins' as *;
@use './assets/vars' as *;
.footer {
  position: relative;
  z-index: 2;
  background: #1f1e1a;
  padding: var(--p160) var(--p291) var(--p40) var(--p291);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  gap: vw(120, 1920);

  &-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: vw(280, 1920);
    position: relative;
  }

  &-left {
    width: vw(467, 1920);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: var(--g40);
  }

  &__logo {
    width: vw(120, 1920);
    height: vw(30, 1920);
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      .logo-figure {
        fill: var(--green-light);
        animation: logo 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards;
      }

      .logo-text {
        animation: logo-backwards 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards;
      }
    }
    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__descr {
    text-transform: none;
  }

  &-right {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: var(--g40);
  }

  &-socials-list {
    display: flex;
    align-items: center;
    gap: var(--g20);
  }
  &-social-link {
    width: vw(40, 1920);
    height: vw(40, 1920);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    background: #272727;
    border-radius: 50%;
    transition: all 0.3s ease-in-out;

    &:hover {
      transform: scale(1.1);
      background: #3a3a3a;
    }
    svg {
      width: 45%;
      height: 45%;
      transition: all 0.3s ease-in-out;
    }
  }

  &-links-list {
    display: flex;
    align-items: center;
    gap: var(--g40);
  }

  &-link {
    text-decoration: none;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;

    &::after {
      content: '';
      position: absolute;
      bottom: -1px;
      left: 0;
      display: block;
      width: 0;
      height: 1px;
      background: #fff;

      transition: width 0.3s ease-in-out;
    }

    &:hover::after {
      width: 100%;
    }
  }

  &-bottom {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &-copyrights {
    color: #848484;
  }
}

@keyframes logo {
  0% {
    transform: translateY(-200%); /* Start offscreen */
  }
  50% {
    transform: translateY(20%); /* Overshoot position */
  }

  100% {
    transform: translateY(0); /* Final position */
  }
}

@keyframes logo-backwards {
  0% {
    transform: translateY(200%); /* Start at the final position */
  }

  50% {
    transform: translateY(-20%); /* Overshoot downwards */
  }
  100% {
    transform: translateY(0); /* Move offscreen */
  }
}
