  font-size: 14px;
  vertical-align: baseline;
  outline: 0;
  box-shadow: none;
  color: #333;
  cursor: pointer;
  vertical-align: middle\9;
}
.paginationjs .paginationjs-go-button > input[type='button']:hover {
