//   background: black;
// }

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: $size;
  aspect-ratio: 1;
  // background: var(--color-brown-bg);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  svg {
    width: 20%;
    height: auto;
    fill: var(--color-white);
    animation: ripple $duration infinite ease-in-out;

    @include tablet {
      width: 40%;
    }
  }
  &-wrap {
    z-index: 10000;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    pointer-events: all;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    overflow: hidden;
    width: 100%;
    // height: 100vh;
    cursor: not-allowed;
    opacity: 1;
    padding: 0;
    gap: 60px;
    overflow: hidden;
    height: 100dvh;
    width: 100vw;
    display: grid;
    place-content: center;
    // background: var(--color-brown-bg);
    background: var(--color-black);
  }

  &-circle {
    position: absolute;
    width: 140vw;
    height: 140vw;
    border-radius: 50%;
    background: linear-gradient(
      45deg,
      rgb(82, 46, 38) 0%,
      // rgba(41, 39, 39, 1) 30%,
      rgba(31, 28, 28, 1) 100%
    );
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @include tablet {
      width: 140vh;
      height: 140vh;
    }
  }

  .box {
    position: absolute;
    background: rgba(245, 223, 186, 0.15);
    background: $background;
    border-radius: 50%;
    border-top: 1px solid rgba(245, 223, 186, 1);
    box-shadow: rgba(245, 223, 186, 0.3) 0px 10px 10px -0px;
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    animation: ripple $duration infinite ease-in-out;

    &:nth-child(1) {
      inset: 40%;
      z-index: 99;
    }
    &:nth-child(2) {
      inset: 30%;
      z-index: 98;
      border-color: rgba(245, 223, 186, 0.8);
      animation-delay: 0.2s;
    }
    &:nth-child(3) {
      inset: 20%;
      z-index: 97;
      border-color: rgba(245, 223, 186, 0.6);
      animation-delay: 0.4s;
    }
    &:nth-child(4) {
      inset: 10%;
      z-index: 96;
      border-color: rgba(245, 223, 186, 0.4);
      animation-delay: 0.6s;
    }
    &:nth-child(5) {
      inset: 0%;
      z-index: 95;
      border-color: rgba(245, 223, 186, 0.2);
      animation-delay: 0.8s;
    }
  }

  // .logo {
  //   position: absolute;
  //   inset: 0;
  //   display: grid;
  //   place-content: center;
  //   padding: 30%;
  //   svg {
  //     fill: var(--color-dark);
  //     width: 100%;
  //     animation: color-change $duration infinite ease-in-out;
  //   }
  // }
}

@keyframes ripple {
  0% {
    transform: scale(1);
    // box-shadow: rgba(0, 0, 0, 0.3) 0px 10px 10px -0px;
  }
  50% {
    transform: scale(1.15);
    // box-shadow: rgba(0, 0, 0, 0.3) 0px 30px 20px -0px;
  }
  100% {
    transform: scale(1);
    // box-shadow: rgba(0, 0, 0, 0.3) 0px 10px 10px -0px;
  }
}

@keyframes color-change {
  0% {
    fill: var(--color-brown-bg);
  }
  50% {
    fill: var(--color-dark);
  }
  100% {
    ffill: var(--color-brown-bg);
  }
}
