@use '../assets/mixins.scss' as *;
@use '../assets/vars.scss' as *;

.breadcrumbs {
  display: flex;
  align-items: center;
  padding: vw(114, 1920) var(--p291) 0 var(--p291);

  &-inner {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: var(--g24);
    padding: var(--p10);
    background: var(--white);
    border-radius: 20px;
  }

  &-btn-back {
    display: flex;
    align-items: center;
    justify-content: center;
    width: vw(40, 1920);
    height: vw(40, 1920);
    background: var(--light-grey);
    border-radius: 50%;
    transition: all 0.3s ease-in-out;

    &:hover {
      background: var(--mid-grey);
      svg {
        transform: scale(1.1);
      }
    }

    svg {
      width: vw(20, 1920);
      height: vw(20, 1920);
      transition: all 0.3s ease-in-out;
    }
  }

  &-list {
    display: flex;
    align-items: center;
    gap: vw(29, 1920);
  }

  &-item {
    display: flex;
    align-items: center;
    justify-content: center;

    &:last-child {
      .breadcrumbs-link {
        color: var(--black);
        pointer-events: none;

        &::after {
          display: none;
        }
      }
    }
  }

  &-link {
    position: relative;
    text-decoration: none;

    &:hover {
      &::after {
        width: 100%;
      }
    }

    &::after {
      content: '';
      position: absolute;
      bottom: -1px;
      left: 0;
      display: block;
      width: 0;
      height: 1px;
      background: var(--basic-grey);
      transition: width 0.3s ease-in-out;
    }

    &::before {
      content: '/';
      position: absolute;
      top: 50%;
      transform: translate(-50%, -50%);
      left: vw(-12, 1920);
      display: block;
      transition: width 0.3s ease-in-out;
      color: var(--basic-grey);
      font-size: vw(16, 1920);
      font-family: $default-font2;
      font-style: normal;
    }
  }

  &-link--home {
    display: flex;
    width: vw(16, 1920);
    height: vw(16, 1920);
    svg {
      width: 100%;
      height: 100%;
      transition: all 0.3s ease-in-out;
    }

    &::before {
      display: none;
    }
    &::after {
      display: none;
    }

    &:hover {
      svg {
        transform: scale(1.1);
      }
    }
  }
}
