@use '../assets/mixins.scss' as *;
.about-intro {
  display: flex;
  height: 100%;
  padding: var(--p160) var(--p291);

  &-inner {
    width: 100%;
    display: flex;
    align-items: flex-start;
  }

  &-left {
    width: 50%;
    display: flex;
    flex-direction: column;
    gap: var(--g16);
    padding-right: vw(177, 1920);
  }

  &__title-wrap {
    display: flex;
    flex-direction: column;

    gap: var(--g12);
  }

  &-right {
    width: 50%;
    display: flex;
    flex-direction: column;
    gap: var(--g24);
    padding-left: vw(129, 1920);
    border-left: 1px dashed var(--mid-grey);
  }

  &__img-wrap {
    width: vw(60, 1920);
    height: vw(60, 1920);
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      width: 100%;
      height: 100%;
    }
  }
}
