@use '../assets/mixins' as *;

.deco {
  &-container {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height: fit-content;
  }

  &-top--left {
    position: absolute;
    top: vw(34, 1920);
    left: vw(160, 1920);
    display: flex;

    svg {
      width: 1px;
      height: vw(285, 1920);
    }
  }

  &-top--right {
    position: absolute;
    top: vw(34, 1920);
    right: vw(160, 1920);
    display: flex;

    svg {
      width: 1px;
      height: vw(588, 1920);
    }
  }

  &--left {
    position: absolute;
    top: vw(176, 1920);
    left: 0;
    height: 1px;
    display: flex;

    svg {
      width: vw(633, 1920);
      height: 1px;
    }
  }
  &--left--s {
    position: absolute;
    top: vw(176, 1920);
    left: 0;
    height: 1px;
    display: flex;

    svg {
      width: vw(231, 1920);
      height: 1px;
    }
  }

  &--right {
    position: absolute;
    top: vw(176, 1920);
    right: 0;
    height: 1px;
    display: flex;

    svg {
      width: vw(633, 1920);
      height: 1px;
    }
  }
}
