@use '../../assets/mixins' as *;
@use '../../assets/vars' as *;
.button {
  pointer-events: auto;
  outline: none;
  margin: 0;
  padding: 0;
  border: none;
  background: none;
  font: inherit;
  line-height: normal;
  cursor: pointer;
  text-decoration: none;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1234px;
  gap: vw(16, 1920);
}
.buttton--primary {
}

.button--secondary {
}

.button--l {
  height: vw(48, 1920);
  width: fit-content;
  padding: vw(12, 1920) vw(24, 1920);
  svg {
    width: vw(18, 1920);
    height: vw(18, 1920);
  }
}
.button--m {
  width: fit-content;
  height: vw(44, 1920);
  padding: vw(10, 1920) vw(16, 1920);

  svg {
    width: vw(18, 1920);
    height: vw(18, 1920);
  }
}
.button--primary--yellow {
  background-color: var(--yellow);
  border: 1px solid var(--yellow);
  box-shadow: 0px 0px 25px 0px rgba(246, 255, 0, 0.25);
  transition: all 0.3s ease-in-out;

  &:hover {
    background-color: var(--white);
    box-shadow: 0px 0px 25px 0px rgba(246, 255, 0, 0);
  }

  &.disabled {
    background-color: #e2e2e2;
    box-shadow: none;
    span {
      color: var(--mid-grey);
    }
    svg {
      fill: var(--mid-grey);
    }
  }

  svg {
    fill: var(--black);
  }

  span {
    font-family: $default-font2;
    font-size: vw(16, 1920);
    font-style: normal;
    letter-spacing: 0.224px;
    font-weight: 400;
    line-height: 150%;
    color: var(--black);
  }
}

.button--primary--black {
  background-color: var(--black);

  transition: all 0.3s ease-in-out;

  &:hover {
    background-color: var(--light-black);
  }

  &.disabled {
    background-color: #e2e2e2;

    span {
      color: var(--mid-grey);
    }
    svg {
      fill: var(--mid-grey);
    }
  }

  svg {
    fill: var(--white);
  }

  span {
    font-family: $default-font2;
    font-size: vw(16, 1920);
    font-style: normal;
    letter-spacing: 0.224px;
    font-weight: 400;
    line-height: 150%;
    color: var(--white);
  }
}

.button--secondary--yellow {
  border: 1px solid var(--yellow);
  box-shadow: 0px 0px 25px 0px rgba(246, 255, 0, 0.25);
  background-color: transparent;
  transition: all 0.3s ease-in-out;

  &:hover {
    border: 1px solid var(--white);
    box-shadow: 0px 0px 25px 0px rgba(246, 255, 0, 0);
    span {
      color: var(--white);
    }
    svg {
      fill: var(--white);
    }
  }

  &.disabled {
    border: 1px solid #e2e2e2;
    box-shadow: none;
    span {
      color: #e2e2e2;
    }
    svg {
      fill: #e2e2e2;
    }
  }

  svg {
    fill: var(--yellow);
  }

  span {
    font-family: $default-font2;
    font-size: vw(16, 1920);
    font-style: normal;
    letter-spacing: 0.224px;
    font-weight: 400;
    line-height: 150%;
    color: var(--yellow);
  }
}

.button--secondary--black {
  border: 1px solid var(--basic-grey);
  background-color: transparent;
  transition: all 0.3s ease-in-out;

  &:hover {
    border: 1px solid var(--black);
  }

  &.disabled {
    border: 1px solid var(--basic-grey);
    box-shadow: none;
    span {
      color: var(--basic-grey);
    }
    svg {
      fill: var(--basic-grey);
    }
  }

  svg {
    fill: var(--black);
  }

  span {
    font-family: $default-font2;
    font-size: vw(16, 1920);
    font-style: normal;
    letter-spacing: 0.224px;
    font-weight: 400;
    line-height: 150%;
    color: var(--black);
  }
}
