@use '../assets/mixins.scss' as *;
.about {
  &-faq-wrap {
    position: relative;
    padding: var(--p160) var(--p291);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    gap: var(--g40);
  }
  &-faq-list {
    width: 70%;
    display: flex;
    flex-direction: column;
    gap: var(--g12);
  }

  &-faq {
    display: flex;
    flex-direction: column;
    // gap: var(--g12);
    background: var(--light-grey);
    border-radius: 18px;
    padding: vw(16, 1920) vw(30, 1920);
    transition: all 0.3s ease-in-out;
    cursor: pointer;

    &.active {
      gap: var(--g12);

      .about-faq-header {
        svg {
          transform: rotate(-90deg);
        }
      }
      .about-faq-text {
        max-height: 400px;
        overflow: visible;
      }
    }
  }

  &-faq-header {
    display: flex;
    justify-content: space-between;
    align-items: center;

    svg {
      transform: rotate(90deg);
      width: calc(18 / 1920 * 100vw);
      height: calc(18 / 1920 * 100vw);
      fill: var(--deep-grey);
      transition: all 0.3s ease-in-out;
      cursor: pointer;
    }
  }

  &-faq-header-title {
    display: flex;
    align-items: center;
    gap: var(--g8);

    .text-14-k {
      line-height: 90%;
    }
  }

  &-faq-text {
    max-height: 0;
    overflow: hidden;
    transition: all 0.3s ease-in-out;
  }
}
