@use '../assets/mixins.scss' as *;

.product-card {
  text-decoration: none;
  display: flex;
  flex-direction: column;
  padding: var(--p10);
  gap: var(--p10);
  width: calc(312 / 1920 * 100vw);
  background: var(--white);
  border-radius: 20px;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 0 0 rgba(0, 0, 0, 0);

  &:hover {
    box-shadow: 0 -1px 10px rgba(8, 8, 2, 0.1);
  }

  &__img-wrap {
    position: relative;
    width: 100%;
    height: vw(260, 1920);
    overflow: hidden;
    border-radius: 12px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: transform 0.3s ease-in-out;
    }
  }

  &__favourite {
    border-radius: 50%;
    position: absolute;
    top: var(--p10);
    right: var(--p10);
    width: vw(40, 1920);
    height: vw(40, 1920);
    background: var(--light-grey);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease-in-out;
    cursor: pointer;

    svg {
      width: 50%;
      height: 50%;
      transition: all 0.3s ease-in-out;

      path {
        stroke: var(--black);
        transition: all 0.3s ease-in-out;
      }
    }

    &.active {
      background: #ffe8e8;

      svg {
        path {
          stroke: #ff3b3b;
          fill: #ff3b3b;
        }
      }
    }

    &:hover {
      background: #ffe8e8;

      svg {
        transform: scale(1.1);
        path {
          stroke: #ff3b3b;
          fill: #ff3b3b;
        }
      }
    }
  }

  &__content {
    padding: var(--p20);
    display: flex;
    flex-direction: column;
    gap: var(--g20);
  }

  &__title-wrap {
    display: flex;
    flex-direction: column;

    gap: var(--g10);
  }

  &__rate-wrap {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: calc(16 / 1920 * 100vw);
  }

  &__rate {
    display: flex;
    align-items: center;
    gap: var(--g5);

    svg {
      width: vw(18, 1920);
      height: vw(18, 1920);
      fill: var(--basic-grey);
    }

    &--1 {
      svg {
        &:first-child {
          fill: var(--black);
        }
      }
    }
    &--2 {
      svg {
        &:nth-child(-n + 2) {
          fill: var(--black);
        }
      }
    }

    &--3 {
      svg {
        &:nth-child(-n + 3) {
          fill: var(--black);
        }
      }
    }

    &--4 {
      svg {
        &:nth-child(-n + 4) {
          fill: var(--black);
        }
      }
    }

    &--5 {
      svg {
        &:nth-child(-n + 5) {
          fill: var(--black);
        }
      }
    }
  }

  &__title {
    text-transform: none;
    display: -webkit-box;
    -webkit-line-clamp: 2; /* Number of lines to show */
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__btns-wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: var(--g20);
  }

  &__btn {
    position: relative;
    padding: vw(13, 1920) vw(24, 1920);
    border: 1px solid var(--basic-grey);
    border-radius: 1234px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease-in-out;

    &:hover {
      background: var(--black);

      svg {
        transform: scale(1.1);
        fill: var(--white);
      }
    }

    svg {
      width: vw(18, 1920);
      height: vw(18, 1920);
      fill: var(--black);
      transition: all 0.3s ease-in-out;
    }
  }

  &__btn-check {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    bottom: 0;
    right: 0;
    transform: translate(25%, 25%);
    width: vw(20, 1920);
    height: vw(20, 1920);
    background: #ccf3f3;
    border-radius: 50%;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease-in-out;
    z-index: 1;
    svg {
      width: 50%;
      height: 50%;
    }

    &.active {
      opacity: 1;
      visibility: visible;
    }
  }
}
