@use '../assets/mixins.scss' as *;

.achievments-homepage {
  background: var(--bg-grey);
  padding: var(--p160) var(--p291);
  &-inner {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--g80);
  }

  &__title-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: var(--g16);
  }

  &__descr {
    color: #686868;
    text-align: center;
    width: 60%;
    text-transform: none;
  }

  &-list-wrap {
    width: 100%;
  }

  &-list {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &-item {
    height: vw(300, 1920);
    width: vw(300, 1920);
    position: relative;
    z-index: 1;
    // border-radius: 1234px;
    // border: 1px dashed var(--Main-Mid-Grey, #a6abac);
    // stroke-dasharray: 3;
  }

  &-item__bg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
    width: vw(361, 1920);
    height: vw(378, 1920);

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &-item__circle {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: transform 0.3s ease;

    .circle-svg {
      width: 100%;
      height: 100%;
      transition: transform 0.3s ease;

      circle {
        cx: 50%;
        cy: 50%;
        r: 49%;
        stroke: var(--basic-grey);
        stroke-width: 0.5;
        fill: none;
      }
    }
  }

  &-item-inner {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: vw(36, 1920);
  }

  &-item__icon {
    width: vw(64, 1920);
    height: vw(64, 1920);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: transform 0.3s ease;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &-item-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: var(--g4);
  }

  &-item__descr {
    text-transform: none;
  }
}
