@use '../assets/mixins.scss' as *;
@use '../assets/vars.scss' as *;

.hero {
  position: relative;
  width: 100%;
  height: 100vh;
  &-inner {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &-img-wrap {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
  }

  &-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: relative;
  }
  &-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: vw(1200, 1920);
    height: 100%;
    object-fit: cover;
    z-index: 1;
  }

  &-content {
    position: relative;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 0 var(--p291);
    gap: var(--g60);
  }

  &-title-wrap {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    text-align: left;
    gap: var(--g40);
    width: 40%;
  }

  &-title {
    text-align: left;
  }

  &-btns-wrap {
    display: flex;
    align-items: center;
    gap: var(--g24);
  }
}
