@use '../assets/mixins.scss' as *;

.blog-card {
  width: vw(312, 1920);
  display: flex;
  padding: var(--p10);
  background: var(--white);
  border-radius: 20px;

  &:hover {
    .blog-card__img-deco-wrap {
      opacity: 1;
      visibility: visible;
    }
  }

  &__link {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    text-decoration: none;
    gap: var(--g10);
  }

  &__img-wrap {
    position: relative;
    width: 100%;
    height: vw(260, 1920);
    overflow: hidden;
    border-radius: 12px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: transform 0.3s ease-in-out;
    }
  }

  &__img-deco-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease-in-out;
  }

  &__img-deco {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    width: vw(44, 1920);
    height: vw(44, 1920);
    background: var(--white);
    border-radius: 50%;

    svg {
      width: 60%;
      height: 60%;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: var(--g24);
    padding: var(--p20);
  }

  &__title-wrap {
    display: flex;
    flex-direction: column;
    gap: var(--g12);
  }

  &__title {
    text-transform: none;
    font-weight: bold;
  }

  &__descr {
    text-transform: none;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: var(--g10);
  }

  &__tag-wrap {
    display: flex;
    align-items: center;
    gap: vw(8, 1920);
  }

  &__tag {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: vw(2, 1920) vw(12, 1920);
    background: var(--black);
    border-radius: 1234px;
  }

  &__tag--blank {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: vw(2, 1920) vw(12, 1920);
    background: var(--light-grey);
    border-radius: 1234px;
  }
}
