@use '../assets/mixins.scss' as *;
@use '../assets/vars.scss' as *;
.contacts-page {
  padding: var(--p30) var(--p291) var(--p160) var(--p291);
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  &-inner {
    display: flex;
    flex-direction: column;
    gap: var(--g30);
  }

  &-intro {
    display: flex;
    gap: var(--g30);
  }

  &-intro-form-wrap {
    width: calc(50% - (var(--g30) / 2));
    padding: var(--p30);
    background: var(--white);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    gap: var(--g40);
  }

  &-intro-form-title-wrap {
    display: flex;
    flex-direction: column;
    gap: var(--g16);
  }

  &-intro-form-subtitle {
    width: 90%;
  }

  &-intro-form {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    gap: var(--g24);
  }

  &-intro-form-title {
    letter-spacing: normal;
  }

  &-intro-form-input-group {
    display: flex;
    flex-wrap: wrap;
    gap: var(--g16);
    row-gap: var(--g24);
  }
  &-intro-form-label {
    width: 100%;
    position: relative;
    display: flex;

    &:nth-child(1) {
      width: calc(50% - (var(--g16) / 2));
    }
    &:nth-child(2) {
      width: calc(50% - (var(--g16) / 2));
    }

    &--textarea {
      textarea {
        height: vw(100, 1920);
      }
    }

    .text-deep-grey {
      padding: 0 3px;
      display: block;
      background: var(--white);
      position: absolute;
      top: 0;
      left: vw(20, 1920);
      transform: translateY(-50%);
    }
  }

  &-intro-form-input {
    width: 100%;
    display: flex;
    height: vw(42, 1920);
    padding: vw(11, 1920) vw(20, 1920);
    background: var(--white);
    border-radius: 10px;
    border: 1px solid #cbd5e1;
    align-items: center;
    color: #a5a5a5;
    font-family: $default-font2;
    font-size: vw(14, 1920);
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }

  &-info {
    width: calc(50% - (var(--g30) / 2));
    padding: var(--p30);
    background: var(--white);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    gap: calc(var(--g24) * 2);
  }

  &-info-item {
    display: flex;
    flex-direction: column;
    gap: var(--g24);
    position: relative;

    &:not(:first-child) {
      &::before {
        content: '';
        position: absolute;
        top: calc(-1 * var(--g24));
        left: 0;
        width: 100%;
        height: 1px;
        transform: translateY(50%);
        background: var(--light-grey);
      }
    }
  }

  &-info-item__title-wrap {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: var(--g12);
  }

  &-info-item__title {
    text-transform: none;
  }

  &-info-item__link {
    display: flex;
    align-items: center;
    gap: var(--g12);
    text-decoration: none;

    &:hover {
      .contacts-page-info-item__icon {
        border: 1px solid var(--deep-grey);
        svg {
          transform: scale(1.1);
        }
      }
    }
  }

  &-info-item__icon {
    display: flex;
    padding: vw(10, 1920) vw(16, 1920);
    justify-content: center;
    align-items: center;
    gap: var(--g16);
    border-radius: 1234px;
    border: 1px solid var(--basic-grey);
    transition: all 0.3s ease-in-out;

    svg {
      width: vw(18, 1920);
      height: vw(18, 1920);
      transition: all 0.3s ease-in-out;
    }
  }

  &-info-item__link-list {
    display: flex;
    gap: var(--g16);
    align-items: center;
  }

  &-map {
    width: 100%;
    height: vw(590, 1920);
    border-radius: 20px;
    padding: var(--p10);
    overflow: hidden;
    position: relative;
    background: var(--white);

    .contacts-page-map__iframe {
      width: 100%;
      height: 100%;
      border: none;
      border-radius: 20px;
    }
  }
}
