@use '../assets/mixins.scss' as *;
@use '../assets/vars.scss' as *;

.contact-homepage {
  position: relative;
  width: 100%;
  background: var(--bg-grey);
  padding: var(--p160) var(--p291);

  &-inner {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--g60);
  }

  &-intro {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--g16);
  }

  &__descr {
    text-transform: none;
    text-align: center;
    width: 60%;
    color: var(--deep-grey);
  }

  &__form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--g60);
  }

  &__form-inner {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: var(--g12);
  }

  &__form-item {
    input {
      display: flex;
      width: vw(417, 1920);
      padding: vw(12, 1920) vw(24, 1920);
      background: var(--white);
      border-radius: 1234px;
      border: 1px solid var(--white);
      align-items: center;
      gap: 10px;
      color: #a5a5a5;
      font-family: $default-font2;
      font-size: vw(16, 1920);
      font-style: normal;
      font-weight: 500;
      line-height: normal;
    }
  }
}
