@font-face {
  font-family: 'KharkivTone';
  src: url('../fonts/KharkivTone.eot');
  src: local('KharkivTone'), local('KharkivTone'),
    url('../fonts/KharkivTone.eot?#iefix') format('embedded-opentype'),
    url('../fonts/KharkivTone.woff2') format('woff2'),
    url('../fonts/KharkivTone.woff') format('woff'),
    url('../fonts/KharkivTone.ttf') format('truetype');
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'Fixel Text';
  src: url('../fonts/FixelText-Regular.eot');
  src: local('Fixel Text Regular'), local('FixelText-Regular'),
    url('../fonts/FixelText-Regular.eot?#iefix') format('embedded-opentype'),
    url('../fonts/FixelText-Regular.woff2') format('woff2'),
    url('../fonts/FixelText-Regular.woff') format('woff'),
    url('../fonts/FixelText-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Fixel Text';
  src: url('../fonts/FixelText-Medium.eot');
  src: local('Fixel Text Medium'), local('FixelText-Medium'),
    url('../fonts/FixelText-Medium.eot?#iefix') format('embedded-opentype'),
    url('../fonts/FixelText-Medium.woff2') format('woff2'),
    url('../fonts/FixelText-Medium.woff') format('woff'),
    url('../fonts/FixelText-Medium.ttf') format('truetype');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
