@use './assets/mixins' as *;
@use './assets/vars' as *;
.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: var(--header-h);
  z-index: 40;
  background: #080700;
  transition: all 1500ms cubic-bezier(0.06, 0.65, 0.2, 1);
  transform: translateY(0%);

  &.header-hidden {
    transform: translateY(-100%);
  }

  // &.top {
  //   background: transparent;
  // }

  &.bg {
    background: var(--bg-black);

    // .header-request--desktop {
    //   border: 1px solid var(--color-black);
    //   svg {
    //     fill: var(--color-black);
    //   }
    //   span {
    //     color: var(--color-black);
    //   }
    // }

    // .header-logo {
    //   svg {
    //     fill: var(--color-black);
    //   }
    // }

    // .header-contacts-phone {
    //   span {
    //     color: var(--color-black);
    //   }
    // }

    // .header-burger {
    //   span {
    //     color: var(--color-black);
    //   }
    // }

    // .header-burger-lines--open {
    //   rect {
    //     fill: var(--color-black);
    //   }
    // }

    &.on {
      .header-burger {
        span {
          color: var(--color-white);
        }
      }
    }
  }

  &.on {
    .header-inner {
      background: transparent;
    }

    .header-left,
    .header-logo-wrap,
    .header-choose-apartment {
      opacity: 0;
      visibility: hidden;
      pointer-events: none;
    }

    .header-menu-wrap {
      opacity: 1;
      visibility: visible;
    }

    .header-burger-lines--open {
      width: 0;
      height: 0;
    }

    .header-burger-lines--close {
      width: 1.25vw;
      height: 1.25vw;

      @include tablet {
        width: 3.125vw;
        height: 3.125vw;
      }

      @include mobile {
        width: 6.66vw;
        height: 6.66vw;
      }
    }
  }

  &-inner {
    position: relative;
    z-index: 10;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: var(--p20) var(--p291);
    transition: all 0.45s ease-in-out;
  }

  &-left {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: var(--g60);
  }

  &-logo {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: var(--logo-w);

    @include tablet {
      order: 1;
      // width: fit-content;
    }

    &__svg--desktop {
      width: 100%;
      height: auto;
      fill: var(--color-white);

      @include tablet {
        display: none;
      }
    }

    &__svg--mobile {
      display: none;

      @include tablet {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
  }

  &-links {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: var(--g60);
  }

  &-link {
    text-decoration: none;
    position: relative;
    display: flex;
    white-space: nowrap;

    &:hover {
      &::before {
        width: 100%;
      }
    }

    &::before {
      content: '';
      position: absolute;
      width: 0;
      height: 1px;
      background-color: var(--white);
      bottom: -2px;
      left: 0;
      transition: width 0.3s ease-in-out;
    }
  }

  &-right {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: var(--g12);
  }

  &-search {
  }

  &-search-form {
    position: relative;
  }

  &-search-form-svg {
    position: absolute;
    top: 50%;
    left: vw(16, 1920);
    transform: translate(0, -50%);
    width: vw(18, 1920);
    height: vw(18, 1920);

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &-search-form-input {
    height: vw(44, 1920);
    width: vw(320, 1920);
    padding: vw(16, 1920) vw(16, 1920) vw(16, 1920) vw(44, 1920);
    border-radius: 1234px;
    background: #27261b;
    border: none;
    font-family: $default-font2;
    font-size: vw(14, 1920);
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    color: var(--light-grey);
  }

  &-cart {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: vw(44, 1920);
    height: vw(44, 1920);
    border-radius: 50%;
    background: #27261b;
    transition: all 0.3s ease-in-out;
    cursor: pointer;

    &:hover {
      background: var(--yellow);

      .header-cart-link {
        svg {
          fill: var(--black);
          transform: scale(1.1);
        }
      }
    }
  }

  &-cart-link {
    text-decoration: none;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    svg {
      width: vw(18, 1920);
      height: vw(18, 1920);
      fill: var(--white);
      transition: all 0.3s ease-in-out;
    }
  }

  &-favourites {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: vw(44, 1920);
    height: vw(44, 1920);
    border-radius: 50%;
    background: #4c4811;
    transition: all 0.3s ease-in-out;
    cursor: pointer;

    &:hover {
      background: var(--yellow);

      .header-favourites-link {
        svg {
          fill: var(--black);
          transform: scale(1.1);
        }
      }
    }
  }

  &-favourites-link {
    text-decoration: none;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    svg {
      width: vw(18, 1920);
      height: vw(18, 1920);
      fill: var(--yellow);
      transition: all 0.3s ease-in-out;
    }
  }

  &-favourites-count {
    position: absolute;
    bottom: 0;
    right: 0;
    transform: translate(25%, 25%);
    background: var(--yellow);
    width: vw(21, 1920);
    height: vw(16, 1920);
    border-radius: 1234px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &-question {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: vw(44, 1920);
    height: vw(44, 1920);
    border-radius: 50%;
    background: #27261b;
    transition: all 0.3s ease-in-out;
    cursor: pointer;

    &:hover {
      background: var(--yellow);

      .header-question-btn {
        svg {
          fill: var(--black);
          transform: scale(1.1);
        }
      }
    }
  }

  &-question-btn {
    text-decoration: none;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    svg {
      width: vw(18, 1920);
      height: vw(18, 1920);
      fill: var(--white);
      transition: all 0.3s ease-in-out;
    }
  }
}

@keyframes logo {
  0% {
    transform: translateY(-200%); /* Start offscreen */
  }
  50% {
    transform: translateY(20%); /* Overshoot position */
  }

  100% {
    transform: translateY(0); /* Final position */
  }
}

@keyframes logo-backwards {
  0% {
    transform: translateY(200%); /* Start at the final position */
  }

  50% {
    transform: translateY(-20%); /* Overshoot downwards */
  }
  100% {
    transform: translateY(0); /* Move offscreen */
  }
}
