@use '../../../assets/styles/assets/mixins.scss' as *;
@use '../../../assets/styles/assets/vars.scss' as *;
.form-popup {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  transform: translateY(-100%);
  background: rgba(23, 23, 23, 0.9);
  z-index: 101;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: all ease-in-out 0.5s;

  &.active {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    transform: translateY(0);
  }

  &-inner {
    position: relative;
    display: flex;
    align-items: center;
    width: 63.23%;
    height: fit-content;
    padding: var(--p40);
    border-radius: var(--border24);
    background: var(--color-black);

    @include tablet {
      width: 90%;
    }

    @include mobile {
      width: 100%;
      padding: calc(var(--p20) * 2) var(--p20) var(--p20) var(--p20);
    }
  }

  &-success {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background: #38b36b;
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    z-index: 2;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transform: translateY(-100%);
    transition: all ease-in-out 0.5s;

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translate(-50%, 100%);
      width: 0;
      height: 0;
      border-left: 20px solid transparent;
      border-right: 20px solid transparent;

      border-top: 20px solid #38b36b;
    }

    &.active {
      transform: translateY(0);
      opacity: 1;
      visibility: visible;
    }
  }
  &-close {
    position: absolute;
    top: var(--p40);
    right: var(--p40);
    width: 42px;
    height: 42px;
    background: var(--color-white);
    border-radius: 50%;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    @include mobile {
      top: var(--p20);
      right: var(--p20);
    }
    svg {
      width: 50%;
      height: 50%;
      path {
        stroke: var(--color-black);
      }
    }
  }
  .form-wrapper {
    width: 100%;
    z-index: 1;
  }
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  gap: var(--g10);
  &-wrapper {
    padding: var(--p48);
    background: var(--main-colors-main_green);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: var(--g40);
    position: relative;
    z-index: 1;
    overflow: hidden;
  }

  &-intro-wrap {
    display: flex;
    flex-direction: column;
    gap: var(--g20);
    align-items: center;
    text-align: center;
  }

  &-intro__svg {
    width: 1.77vw;
    height: 1.77vw;

    @include tablet {
      width: 3.33vw;
      height: 3.33vw;
    }

    @include mobile {
      width: 6.1vw;
      height: 6.1vw;
    }

    svg {
      width: 100%;
      height: 100%;
      fill: var(--main-colors-main_sand);
    }
  }

  &-inner {
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    gap: 40px;
    width: 100%;
  }

  &-agreement {
    margin-top: var(--g20);
    text-align: center;
  }
}

.submit-btn,
.form-thanks-btn {
  margin-top: 40px;
  cursor: pointer;
  border-left: none;
  border-right: none;
  width: 360px;

  @include to(1440px) {
    width: 310px;
  }
}

.form-field-input--checkbox {
  display: flex;
  column-gap: 8px;
  position: relative;
  margin-bottom: 32px;
  .form-field__input {
    height: auto !important;
  }
  .input-message {
    position: absolute;
    top: 100%;
    left: 0;
  }
  a {
    color: #0d6efd;
  }
  input {
    width: auto;
    cursor: pointer;
  }
  &-subtitle {
    color: var(--color-gray-body);
  }
}

.form-fields-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  gap: 20px;

  @include mobile {
    flex-direction: column;
  }
}

.form-field:not(#t) {
  position: relative;
  background: transparent;
  transition: all 0.3s;
  width: 100%;
}

[data-btn-submit] {
  margin-top: var(--g40);
  span {
    @include mobile {
      font-size: clamp(14px, 3.88vw, 3.88vw);
    }
  }
}

[data-animation='focus'] .input-message {
  color: var(--color-gray);
  position: absolute;
  top: 50%;
  left: 0;
  z-index: 3;
  display: flex;
  transform: translate(0%, -50%);
  user-select: none;
  pointer-events: none;
  transition: all 0.16s cubic-bezier(0, 0, 0.2, 1) 0ms;
}
textarea.form-field__input {
  // padding-top: 10px;
}
.form-field__input {
  text-align: left;
  height: 68px;
  width: 100%;
  background: transparent;
  border-radius: 8px;

  border: 1px solid rgba(242, 245, 251, 0.2);

  padding: 20px;
  cursor: pointer;
  color: var(--color-white);
  font-family: $default-font;
  font-size: 0.94vw;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  &:focus::placeholder {
    opacity: 0;
  }

  @include tablet {
    font-size: clamp(18px, 2.34vw, 2.34vw);
  }

  @include mobile {
    height: 18vw;
    font-size: clamp(14px, 3.88vw, 3.88vw);
  }

  &::placeholder {
    color: var(--darkblue_color);
    text-align: left;
  }
  &:focus + .form-field-message {
    opacity: 1;
  }
}
.mask-absolute {
  color: var(--color-gray);
  text-align: left;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 10;
  height: 60px;
  width: 100%;
  //background: rgba(113, 167, 0, 0.05);
  //border: 1px solid #F3EFE2;
  padding-left: 24px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  cursor: pointer;

  @include to(1440px) {
    height: 48px;
    padding-left: 16px;
  }
}
[data-animation='focus'] .form-field__input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
[data-animation='none'] .input-message {
  color: var(--color-gray);
  text-align: right;
  position: absolute;
  left: 0;
  display: flex;
  justify-content: flex-start;
  width: 100%;
  text-transform: none;
  transform: translate(0%, 100%);
}
[data-field-input][data-status='field--inactive']:not(#r) {
  .input-message {
    color: var(--color-gray);
  }
}
[data-field-input][data-status='field--active']:not(#r) {
  .input-message {
    color: var(--color-gray);
  }
}
[data-field-input][data-status='field--error']:not(#r) {
  .input-title {
    opacity: 0;
  }
  .input-message {
    color: #ea4041;
    position: absolute;
    transform: translateY(100%);
    font-weight: 500;
    font-size: 10px;
    line-height: 100%;

    @include tabletLarge {
      transform: translateY(50%);
    }
  }
}
[data-field-input][data-status='field--success']:not(#r) {
  .input-message {
    color: #96c480;
  }
}
.selected:not(#f) .input-message {
  top: 0;

  color: var(--color-gray);
  text-align: left;
}
.requaired-block {
  &-ico {
    width: 25px;
    height: 25px;
    svg {
      width: 100%;
      height: 100%;
    }
  }
}
.requaired-block {
  &__ico {
    width: 25px;
    height: 25px;

    svg {
      width: 100%;
      height: 100%;
    }
  }
  &__text {
    color: var(--color-gray);
    text-align: left;
    margin: 0px vw(18, 1920);
    white-space: nowrap;
  }
}

.form-field-message {
  position: absolute;
  top: 0;
  opacity: 0;

  color: var(--color-gray);
  text-align: left;
  transform: translateY(calc(-100% - 10px));
}

.requaired-block-ico--position {
  position: absolute;
  top: 0;
  right: 0;
  pointer-events: none;
}

.form-thanks-wrapper {
  opacity: 0;
  visibility: hidden;
  height: 100vh;
  width: 100vw;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  background: rgba(10, 17, 27, 0.5);
  transition: opacity 0.5s ease-out, visibility 1ms linear 0.5s;
  &.active {
    opacity: 1;
    visibility: visible;
    transition: opacity 0.5s ease-out, visibility 1ms linear 0.5s;
  }
  .form-thanks {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 50%;
    height: 40%;
    background: #ffffff;
  }
}

.form {
  .iti input {
    padding-right: 60px;
  }
}

// @import './form-mobile';

.tablet,
.mobile {
  .form-wrapper {
    background-color: var(--color-white);
    .form {
      width: 100%;
      padding-left: var(--page-side-fields);
      padding-right: var(--page-side-fields);
      flex-wrap: wrap;
    }
    .form-close {
      top: 70px;
    }
    .form-title {
      margin-bottom: 16px;
    }
  }
}

.mobile {
  .form-description {
    font-size: 12px;
    padding-left: 16px;
    padding-right: 16px;
  }
  [data-btn-submit] {
    margin-top: 20px;
  }
}
