@use '../assets/mixins.scss' as *;

.awards-homepage {
  position: relative;
  width: 100%;
  height: vw(483, 1920);
  z-index: 10;
  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #1f1e1a;
    z-index: 0;
  }

  &__bg--left {
    position: absolute;
    bottom: -12%;
    left: 0;
    width: vw(600, 1920);
    height: auto;
  }

  &__bg--right {
    position: absolute;
    top: -15%;
    right: 0;
    width: vw(600, 1920);
    height: auto;
  }

  &__bg--circle-left {
    position: absolute;
    bottom: 0;
    left: var(--p291);
    transform: translate(-50%, 50%);
    width: vw(54, 1920);
    height: vw(54, 1920);
    z-index: 1;
  }

  &__bg--circle-right {
    position: absolute;
    top: 0;
    right: var(--p291);
    transform: translate(50%, -50%);
    width: vw(54, 1920);
    height: vw(54, 1920);
    z-index: 1;
  }

  &__deco-wrap {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
  }

  &__deco--1 {
    position: absolute;
    bottom: -35%;
    left: -15%;
    width: vw(528, 1920);
    height: auto;
  }

  &__deco--2 {
    position: absolute;
    top: -33%;
    right: -7%;
    width: vw(528, 1920);
    height: auto;
  }

  &__inner {
    padding: 0 var(--p291);
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
  }

  &-intro {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: var(--g24);
  }

  &__descr {
    width: 80%;
    text-transform: none;
  }

  &__img-wrap {
    width: 50%;
    height: 100%;
    position: relative;
  }

  &__img--1 {
    position: absolute;
    top: vw(73, 1920);
    left: 0;
    width: vw(345, 1920);
    height: vw(243, 1920);
  }
  &__img--2 {
    position: absolute;
    top: 35%;
    left: 37%;
    width: vw(345, 1920);
    height: vw(243, 1920);
  }
}
