.footer-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: calc(280 / 1920 * 100vw);
  position: relative;
}
.footer-left {
  width: calc(467 / 1920 * 100vw);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--g40);
}
.footer__logo {
  width: calc(120 / 1920 * 100vw);
  height: calc(30 / 1920 * 100vw);
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer__logo:hover .logo-figure {
  fill: var(--green-light);
  animation: logo 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards;
}
.footer__logo:hover .logo-text {
  animation: logo-backwards 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards;
}
.footer__logo svg {
  width: 100%;
  height: 100%;
}
.footer__descr {
  text-transform: none;
}
.footer-right {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: var(--g40);
}
.footer-socials-list {
  display: flex;
  align-items: center;
  gap: var(--g20);
}
.footer-social-link {
  width: calc(40 / 1920 * 100vw);
  height: calc(40 / 1920 * 100vw);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  background: #272727;
  border-radius: 50%;
  transition: all 0.3s ease-in-out;
}
.footer-social-link:hover {
  transform: scale(1.1);
  background: #3a3a3a;
}
.footer-social-link svg {
  width: 45%;
  height: 45%;
  transition: all 0.3s ease-in-out;
}
.footer-links-list {
  display: flex;
  align-items: center;
  gap: var(--g40);
}
.footer-link {
  text-decoration: none;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer-link::after {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 0;
  display: block;
  width: 0;
  height: 1px;
  background: #fff;
  transition: width 0.3s ease-in-out;
}
.footer-link:hover::after {
  width: 100%;
}
.footer-bottom {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer-copyrights {
  color: #848484;
}

@keyframes logo {
  0% {
    transform: translateY(-200%); /* Start offscreen */
  }
  50% {
    transform: translateY(20%); /* Overshoot position */
  }
  100% {
    transform: translateY(0); /* Final position */
  }
}
@keyframes logo-backwards {
  0% {
    transform: translateY(200%); /* Start at the final position */
  }
  50% {
    transform: translateY(-20%); /* Overshoot downwards */
  }
  100% {
    transform: translateY(0); /* Move offscreen */
  }
}
.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 300px;
  aspect-ratio: 1;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.loader svg {
  width: 20%;
  height: auto;
  fill: var(--color-white);
  animation: ripple 2s infinite ease-in-out;
}
@media (max-width: 1023px) {
  .loader svg {
    width: 40%;
  }
}
.loader-wrap {
  z-index: 10000;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  pointer-events: all;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  overflow: hidden;
  width: 100%;
  cursor: not-allowed;
  opacity: 1;
  padding: 0;
  gap: 60px;
  overflow: hidden;
  height: 100dvh;
  width: 100vw;
  display: grid;
  place-content: center;
  background: var(--color-black);
}
.loader-circle {
  position: absolute;
  width: 140vw;
  height: 140vw;
  border-radius: 50%;
  background: linear-gradient(45deg, rgb(82, 46, 38) 0%, rgb(31, 28, 28) 100%);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media (max-width: 1023px) {
  .loader-circle {
    width: 140vh;
    height: 140vh;
  }
}
.loader .box {
  position: absolute;
  background: rgba(245, 223, 186, 0.15);
  background: linear-gradient(0deg, rgba(245, 223, 186, 0.2) 0%, rgba(181, 157, 126, 0.2) 100%);
  border-radius: 50%;
  border-top: 1px solid rgb(245, 223, 186);
  box-shadow: rgba(245, 223, 186, 0.3) 0px 10px 10px 0px;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  animation: ripple 2s infinite ease-in-out;
}
.loader .box:nth-child(1) {
  inset: 40%;
  z-index: 99;
}
.loader .box:nth-child(2) {
  inset: 30%;
  z-index: 98;
  border-color: rgba(245, 223, 186, 0.8);
  animation-delay: 0.2s;
}
.loader .box:nth-child(3) {
  inset: 20%;
  z-index: 97;
  border-color: rgba(245, 223, 186, 0.6);
  animation-delay: 0.4s;
}
.loader .box:nth-child(4) {
  inset: 10%;
  z-index: 96;
  border-color: rgba(245, 223, 186, 0.4);
  animation-delay: 0.6s;
}
.loader .box:nth-child(5) {
  inset: 0%;
  z-index: 95;
  border-color: rgba(245, 223, 186, 0.2);
  animation-delay: 0.8s;
}

@keyframes ripple {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.15);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes color-change {
  0% {
    fill: var(--color-brown-bg);
  }
  50% {
    fill: var(--color-dark);
  }
  100% {
    ffill: var(--color-brown-bg);
  }
}
.modal-mobile {
  position: fixed;
  height: 100dvh;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  display: flex;
  align-items: flex-end;
  background: rgba(23, 23, 23, 0.9);
  opacity: 0;
  visibility: hidden;
  transition: all ease-in-out 0.4s;
}
.modal-mobile.active {
  opacity: 1;
  visibility: visible;
  transition: all ease-in-out 0.4s;
}
.modal-mobile-inner {
  width: 100%;
}
.modal-mobile-list {
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow: hidden;
  border-radius: 12px 12px 0px 0px;
  border-top: 1px solid rgba(231, 178, 93, 0.09);
  border-bottom: 1px solid rgba(231, 178, 93, 0.09);
  border-left: 1px solid rgba(231, 178, 93, 0.09);
  backdrop-filter: blur(17px);
}
.modal-mobile-btn {
  text-transform: uppercase;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: var(--g8);
  width: 100%;
  background: rgba(0, 0, 0, 0.6);
  height: 18.88vw;
}
.modal-mobile-btn--open {
  position: relative;
}
.modal-mobile-btn--open::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: linear-gradient(270deg, rgba(245, 223, 186, 0) 0%, #f5dfba 50%, rgba(143, 130, 109, 0) 100%);
  z-index: 1;
}
.modal-mobile-btn--open::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: linear-gradient(270deg, rgba(245, 223, 186, 0) 0%, #f5dfba 50%, rgba(143, 130, 109, 0) 100%);
  z-index: 1;
}
.modal-mobile-btn--close span {
  color: var(--color-pantone-1);
}