@use '../assets/mixins.scss' as *;
.blog-all {
  padding: var(--p30) var(--p291) var(--p160) var(--p291);
  &-title {
    padding: var(--p30) var(--p291) 0 var(--p291);
  }
  &-list {
    display: flex;
    flex-wrap: wrap;
    gap: var(--g30);
  }

  &-item {
    width: calc(25% - (var(--g30) * 3 / 4));
  }
}
