@use '../assets/mixins.scss' as *;

.about-homepage {
  width: 100%;
  overflow: hidden;
  &-inner {
    height: vw(700, 1920);
    width: 100%;
    display: flex;
    background: #1f1e1a;
  }

  &__img-wrap {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__content {
    position: relative;
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: var(--p120);
    gap: var(--g60);
  }

  &__deco {
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(33%, -33%);
    width: vw(528, 1920);
    height: vw(528, 1920);
    z-index: 0;
  }

  &-text {
    position: relative;
    z-index: 1;
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: var(--g24);
  }

  &__descr {
    text-transform: none;
  }
}
