@use '../assets/mixins.scss' as *;
.bestsellers-homepage {
  position: relative;
  background: var(--bg-grey);
  padding: var(--p160) var(--p291) 0 var(--p291);
  display: flex;
  flex-direction: column;
  align-items: center;

  &-inner {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--g60);
  }

  &__title-wrap {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    gap: var(--g20);
  }

  &__btns-wrap {
    display: flex;
    align-items: center;
    gap: vw(16, 1920);
  }

  &__delimiter {
    width: 1px;
    height: vw(20, 1920);
    background: var(--basic-grey);
  }

  &-list-wrap {
    width: 100%;
  }

  &-list {
    width: 100%;
  }
}
